import React, { useState, useEffect } from 'react';
import '../stylesheets/App.scss';
import '../stylesheets/backgroundSpace.scss';
import { Route, Switch } from 'react-router-dom/cjs/react-router-dom.min';
import Home from './Home';
import ListCharacters from './ListCharacters';
import CharactersDetails from './CharactersDetails';
import getDataCharacterApi from '../data/getDataCharacterApi';
import TitleMain from './TitleMain';
import Filters from './Filters';

let numPag;

const numberPage = () => {
  return (numPag = 2);
};
numberPage();

console.log(numPag);

function App() {
  const [data, setData] = useState([]);

  useEffect(() => {
    getDataCharacterApi().then((data) => {
      setData(data.results);
    });
  }, []);

  const renderCharacterDetail = (props) => {
    const characterId = props.match.params.id;
    const foundCharacter = data.find((character) => {
      return character.id === parseInt(characterId);
    });
    return <CharactersDetails data={foundCharacter} />;
  };

  return (
    <>
      <div className='stars--CardList'></div>
      <div className='twinkling--CardList'></div>
      <div className='clouds--CardList'></div>
      <Switch>
        <Route exact path='/' component={Home} />
        <Route exact path='/Characters'>
          <TitleMain />
          <Filters />
          <ListCharacters data={data} />
        </Route>
        <Route path='/character/:id' render={renderCharacterDetail} />
      </Switch>
    </>
  );
}

export default App;

export { numPag };
